*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

*::selection {
  background-color: rgb(33, 43, 61);
  text-shadow: none;
  color: #ffffff;
}

*::-moz-selection {
  background-color: rgb(33, 43, 61);
  text-shadow: none;
  color: #ffffff;
}

*::-webkit-selection {
  background-color: rgb(33, 43, 61);
  text-shadow: none;
  color: #ffffff;
}

ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}

/* ---------------------- */
/* SCROLLBAR START */
.scrollbar-thumb {
  background-color: rgb(16, 22, 33) !important;
  border-radius: 0px !important;
}
.scrollbar-track {
  border-radius: 0px !important;
  background-color: rgb(10, 15, 24) !important;
}
/* SCROLLBAR END */
/* ---------------------- */

.App {
  width: 100%;
  height: 100dvh;
  background-color: rgb(8, 12, 16);
}

/* FONT SIZES */
.font-mini {
  font-size: 0.6rem;
}
.font-small {
  font-size: 1.2rem;
}
.font-medium {
  font-size: 1.8rem;
}
.font-big {
  font-size: 2rem;
}
.font-large {
  font-size: 3.2rem;
}
.font-giant {
    font-size: 5rem;
}
/* FONT COLORS */
.clr-white {
  color: white;
}
.clr-black {
  color: black;
}
.clr-main-dark {
  color: rgb(35, 46, 57);
}
.clr-main-light {
  color: rgb(75, 92, 108);
}
/* FONT WEIGHTS */
.font-heavyweight {
  font-weight: 600;
}